<script>
	import Store from './store.js';

	let typeCollapse = false;
	let type = {text: 'All', value: 1};
	let types = [
		{text: 'All', value: 1},
		{text: 'Todo', value: 2},
		{text: 'Accomplished', value: 3},
	];

	let todoTitle = ''
	let todos = Store.isExist('todoList') ?
		Store.get('todoList') :
		[
			{id: timeId(), title: 'todo1', checked: true},
			{id: timeId(), title: 'todo2', checked: false}
		];

	function timeId() {
		const trailing = 10**5;
		return parseInt(trailing*(Date.now()+Math.random()))
	}

// todo type
	function toggleType() {
		typeCollapse = !typeCollapse
	}
	function changType(value) {
		type = types.find(type => {
			return type.value === value
		})
		toggleType()
	}

	$: isTypeOf = (todo) => {
		switch (type.value) {
			case 1: // All
				return true
			case 2: // todo
				return todo.checked === false
			case 3: // Accomplished
				return todo.checked === true
		}
	}

// todo CRUD
	function addTodo() {
		todos = todos.concat({id: timeId(), title: todoTitle, checked: false});
		todoTitle = '';
		Store.set('todoList', todos);
	}

	function updateTodo(todo, value) {
		const id = todo.id
		todos = todos.map((todo) => {
			if (todo.id === id) { todo = {...todo, ...value}; }
			return todo;
		})
		Store.set('todoList', todos);
	}

	function removeTodo(todo) {
		const id = todo.id;
		let todoIndex = -1;
		todos.find((todo, index) => {
			if (todo.id === id) {
				todoIndex = index;
				return index;
			}
		})
		if (todoIndex >= 0 ) {
			todos.splice(todoIndex, 1);
			Store.set('todoList', todos);
			todos = todos;
		}
	}

</script>

<style type="text/sass">@import url("https://fonts.googleapis.com/css?family=Arimo&display=swap");
* {
  font-family: 'Arimo', sans-serif;
  outline: none !important; }

.todo-list {
  background-color: white;
  border-radius: 5px;
  height: 80%;
  margin: calc(10% - 22px) auto;
  padding: 0 30px 30px;
  position: relative;
  width: 350px; }
  .todo-list .header {
    background-color: #fff;
    border-radius: 10px;
    color: #565656;
    margin: 0 -30px;
    position: fixed;
    text-align: center;
    width: 350px;
    z-index: 100; }
    .todo-list .header .title {
      line-height: 60px;
      margin: 0px; }
    .todo-list .header .btn-toggle {
      background-color: transparent;
      border: none;
      height: 20px;
      padding: 0px;
      position: absolute;
      right: 20px;
      top: 20px;
      width: 20px; }
      .todo-list .header .btn-toggle:after, .todo-list .header .btn-toggle:before {
        border: solid #565656;
        content: "";
        height: 8px;
        left: 0px;
        position: absolute;
        transition: all .2s;
        width: 20px; }
      .todo-list .header .btn-toggle:after {
        border-width: 1px 0 0;
        top: 2px; }
      .todo-list .header .btn-toggle:before {
        border-width: 1px 0;
        top: 10px; }
      .todo-list .header .btn-toggle.show:after {
        border-width: 1px 0 0;
        left: -2px;
        top: 8px;
        transform: rotateZ(45deg); }
      .todo-list .header .btn-toggle.show:before {
        border-width: 0 0 1px;
        height: 0px;
        left: 1px;
        top: 9px;
        transform: rotateZ(-45deg); }
    .todo-list .header .type-group {
      max-height: 0px;
      overflow: hidden;
      transition: all .3s; }
      .todo-list .header .type-group.show {
        max-height: 120px; }
      .todo-list .header .type-group .type-item {
        cursor: pointer; }
  .todo-list .todo-form {
    margin: 30px 0;
    padding-top: 80px;
    position: relative;
    width: 100%; }
    .todo-list .todo-form .todo-title {
      border: none;
      border-bottom: 1px solid #e6e6e6;
      outline: none;
      width: 100%; }
    .todo-list .todo-form .todo-submit {
      bottom: 0px;
      color: #a0a0a0;
      position: absolute;
      right: 0px; }
      .todo-list .todo-form .todo-submit:hover, .todo-list .todo-form .todo-submit:focus {
        outline: none; }
  .todo-list .todo-items {
    height: 380px;
    overflow: scroll;
    padding-right: 5px;
    width: 100%; }
    .todo-list .todo-items .todo-item {
      display: flex;
      justify-content: space-between; }
      .todo-list .todo-items .todo-item .checked label {
        color: #a0a0a0;
        text-decoration: line-through; }
  @media (max-width: 576px) {
    .todo-list {
      border-radius: 0px;
      height: 100%;
      margin: 0px;
      position: absolute;
      top: 0px;
      width: 100%; }
      .todo-list .header {
        background-color: #1fc8db;
        background-image: linear-gradient(141deg, #9fb8ad 0%, #1fc8db 51%, #2cb5e8 75%);
        border-color: #fff;
        border-radius: 0;
        color: #fff;
        width: 100%; }
        .todo-list .header .btn-toggle:after, .todo-list .header .btn-toggle:before {
          border-color: #fff; }
      .todo-list .todo-form {
        padding-top: 50px; }
      .todo-list .todo-items {
        height: calc(100% - 140px); } }
</style>

<div class="todo-list">
	<div class="header">
		<h5 class="title">{type.text} Tasks</h5>
		<button
			class="btn-toggle"
			class:show={typeCollapse}
			on:click={() => toggleType()}
		>
		</button>
		<div
			class="type-group"
			class:show={typeCollapse}
		>
			{#each types as {text, value}, index}
				<p
					class="type-item"
					class:select={value === type.value}
					on:click={() => changType(value)}
				>
					{text} Tasks
				</p>
			{/each}
		</div>
	</div>

	<form class="todo-form" on:submit|preventDefault={addTodo}>
		<input class="todo-title" type="text" name="to-do" bind:value={todoTitle}>
		<button class="btn todo-submit">SUBMIT</button>
	</form>

	<div class="todo-items">
		{#each todos as todo, index}
			{#if isTypeOf(todo)}
				<div class="todo-item">
					<div
						class="form-check"
						class:checked={todo.checked}
					>
						<input
							id={`item-${index}`}
							class="form-check-input"
							type="checkbox"
							checked={todo.checked}
							on:click={() => updateTodo(todo,{checked: !todo.checked})}
						>
						<label
							class="form-check-label"
							for={`item-${index}`}
						>
							{todo.title}
						</label>
					</div>
					<button
						class="btn"
						on:click={() => removeTodo(todo)}
					>
						X
					</button>
				</div>
			{/if}
		{/each}
	</div>
</div>
